  /* 2 sets of style the above and below can stand alone */




  
  .skills-section {
    padding: 50px 0;
  
  }
  
  .skills-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .skills-header {
    text-align: center;
    margin-bottom: 40px;
  }
  
  
  .skills-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .skill-item {
    flex: 1 1 calc(33.333% - 20px);
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    position: relative;
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
    perspective: 1000px;
    margin: 0 10px 20px;
  }
  
  .skill-item.hovered {
    transform: translateX(20px); 
    opacity: 0.9; 
  }
  
  .skill-content {
    position: relative;
    width: 100%;
    height: 100%;
  }
  
  .skill-icon {
    font-size: 3em;
    margin-bottom: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateY(90deg);
    backface-visibility: hidden;
    transition: transform 0.6s;
  }
  
  .skill-item.hovered .skill-icon {
    transform: translate(-50%, -50%) rotateY(0deg);
  }
  
  .skill-name,
  .skill-bar {
    transition: transform 0.6s, opacity 0.6s;
    backface-visibility: hidden;
  }
  
  .skill-item.hovered .skill-name,
  .skill-item.hovered .skill-bar {
    transform: rotateY(90deg);
    opacity: 0;
  }
  
  .skill-name {
    font-size: 1.5em;
    color: #000f;
    margin-bottom: 10px;
  }
  
  .skill-bar {
    margin-top: 10px;
  }
  
  
  
 
  
  /* Responsive Design */
  @media (min-width: 768px) and (max-width: 1200px) {
    .skill-item {
      flex: 1 1 calc(50% - 20px); /* 2 items per row on tablet */
    }
  }
  
  @media (max-width: 767px) {
    .skill-item {
      flex: 1 1 100%; /* 1 item per row on mobile */
    }
  }
  
  
  